* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

ul, li, a {
  text-decoration: none;
  list-style: none;
}

.centered {
  width: 100%;
  aspect-ratio: 16 / 9;
  margin: 0 auto;
  display: block;
}

.hw-link {
  width: 100%;
  max-width: 300px;
  color: #fff;
  text-align: center;
  background-color: #f44336;
  border-radius: 4px;
  margin: 10px auto;
  padding: 12px;
  text-decoration: none;
  transition: all .3s ease-in-out;
  display: block;
  box-shadow: 0 2px 5px rgba(0, 0, 0, .5);
}

.hw-link:hover {
  background-color: #ff5722;
}

.hw-link:focus {
  outline: none;
  box-shadow: 0 0 0 2px #f44336;
}

.hw-link:active {
  transform: translateY(1px);
  box-shadow: 0 1px 3px rgba(0, 0, 0, .5);
}

.gallery_task {
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.gallery-item-task {
  flex-basis: calc(33.33% - 10px);
  margin-bottom: 20px;
}

.gallery-image-task {
  width: 100%;
  height: auto;
  display: block;
}

#myBtn {
  color: #fff;
  cursor: pointer;
  width: 180px;
  visibility: hidden;
  background-color: #9c27b0;
  border: none;
  padding: 12px 16px;
  font-size: 18px;
  font-weight: bold;
}

.modal {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .4);
  padding-top: 100px;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content {
  width: 50%;
  background-color: #e1bee7;
  border: 5px solid #7b1fa2;
  margin: auto;
  padding: 0;
  animation-name: animatetop;
  animation-duration: .4s;
  position: relative;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2), 0 6px 20px rgba(0, 0, 0, .19);
}

@keyframes animatetop {
  from {
    opacity: 0;
    top: -300px;
  }

  to {
    opacity: 1;
    top: 0;
  }
}

.close {
  color: #fff;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover, .close:focus {
  color: #000;
  cursor: pointer;
  text-decoration: none;
}

.modal-header {
  color: #fff;
  text-align: center;
  background-color: #9c27b0;
  padding: 2px 16px;
}

.modal-body {
  padding: 2px 16px;
}

.modal-footer {
  color: #fff;
  text-align: center;
  background-color: #9c27b0;
  padding: 2px 16px;
}

